import glamorous from 'glamorous/dist/glamorous.cjs.tiny'

const Icon = glamorous('div')({
  width: '32px',
  height: '32px'
}, props => {
  switch (props.glam.type) {
    case 'error':
      return ({
        'background': `url('data:image/png;base64,') no-repeat`
      })
    case 'warning':
      return ({
        'background': `url('data:image/png;base64,') no-repeat`
      })
    case 'info':
      return ({
        'background': `url(data:image/svg+xml;base64,); no-repeat`
      })
    case 'success':
      return ({
        'background': `url('data:image/png;base64,') no-repeat`
      })
    case  'success-game':
      return ({
        'background': `url('data:image/png;base64,') no-repeat`
      })
    case  'error-game':
      return ({
        'background': `url('data:image/png;base64,') no-repeat`
      })
    case  'warning-game':
        return ({
          'background': `url('data:image/png;base64,') no-repeat`
        })
  }
})

export const IconPlaceholder = glamorous('div')({
  display: 'flex',
  justifyContent: 'center',
  width: '50px'
})

export default Icon
